<template>
	<div class="start_stop" ref='line'></div>
</template>

<script>
import { onMounted, reactive, toRefs, ref,watch } from 'vue'
import * as echarts from 'echarts'
export default {
	props:{
		grid:{
			type:Object,
			default:()=>{ return {
				left: '10%',
				right: '5%',
				top: '20%',
				bottom: '20%',
			}}
		},
		title:{
			type:String,
			default:()=>''
		},
		xAxis:{
			type:Array,
			default:()=>[
					"2023-08-24 00:00:00",
					"2023-08-24 00:01:00",
					"2023-08-24 00:02:00",
					"2023-08-24 00:03:00",
					"2023-08-24 00:04:00",
				]
		},
		data:{
			types:Array,
			default:()=>["0","0","0",'1','1','0']
		},
		width:{
			type:String,
			default:()=>'100%'
		},
		height:{
			type:String,
			default:()=>'100%'
		},
	},
	setup(props) {
		// var dom = document.getElementById("containers");
		const line = ref('')

		var app = {};
		const state = reactive({

		})
		const methods = {
			drawing() {
				var myChart = echarts.init(line.value);
				var colors = ['#21DE73', '#4D7BBD', '#ccc']; //
				const datas = props.data.map((item, index) => {
					return {
						name: "",
						itemStyle: {
							color: item == '0' ? new echarts.graphic.LinearGradient(0, 0, 0, 1, [
								{
									offset: 0,
									color: 'rgb(0, 0, 255)'
								},
								{
									offset: 1,
									color: 'rgb(65, 105, 225)'
								}
							]) : item == '1' ? new echarts.graphic.LinearGradient(0, 0, 0, 1, [
								{
									offset: 0,
									color: 'rgb(34, 139, 34)'
								},
								{
									offset: 1,
									color: 'rgb(0, 250, 154)'
								}

							]) : new echarts.graphic.LinearGradient(0, 0, 0, 1, [
								{
									offset: 0,
									color: 'rgb(169, 169, 169)'
								},
								{
									offset: 1,
									color: 'rgb(128, 128, 128)'
								}
							])

						}, value: [Number(item), props.xAxis[index],props.xAxis[index+1]]
					}
				})
				// echart
				var option = {
					title: {
						// text: '',
						left: 'left',
						// top: '%',
						left: '10%'
					},
					color: colors,
					tooltip: {//
						formatter: function (params) {
							const str = params.value[0] == '0' ? "" : params.value[0] == '1' ?'' : ""
							const color = params.value[0] == '0' ? colors[1] : params.value[0] == '1' ?colors[0] :colors[2]
							return params.value[1] + '<br/>' + params.name + `<span style='display: inline-block;width:15px;height:15px;background:${color};border-radius:50%;transform: translateY(3px);'></span>` + `<span style='margin-left:5px;display: inline-block;'>${str}</span>`;
						}//`
					},
					legend: {//
						data: ['', '', ''],
						top: '5%',
						selectedMode: false, // 
						textStyle: {
							color: '#000'
						},
						textStyle: {
							color: '#fff'
						}
					},
					grid: {//
						left: '10%',
						right: '5%',
						top: '20%',
						bottom: '20%',
						borderWidth: 0
					},
					xAxis: {
						type: 'category',
						scale: true,
						axisLine: {
							lineStyle: {
								show: false,
								color: '#fff'
							}
						},
						axisLabel: {
							// align: 'left', // 
							verticalAlign: 'middle' // 
						},
						axisTick: {
							show: false,

						},
						splitLine: {
							show: false
						},
						data: props.xAxis
					},
					yAxis: {
						name: '',
						type: 'value',
						max: 1,
						min: 0,
						minInterval: 1,
						axisLine: {
							show: false,
							lineStyle: {
								show: false,
								color: '#fff',
							}
						},
						axisLabel: {
							show: false,
							// align: 'left', // 
							verticalAlign: 'middle' // 
						},
						axisTick: {
							show: false,
						},
						splitLine: {
							show: false
						},
						// data: [''],
					},
					series: [

						{ name: '', type: 'bar', data: [] },
						{ name: '', type: 'bar', data: [] },// bar
						{ name: '', type: 'bar', data: [] },// bar
						{
							type: 'custom',
							borderWidth: 0,
							labelLayout: () => {
								return { verticalAlign: 'middle' }
							},
							renderItem: function (params, api) {// renderItem 
								var categoryIndex = api.value(0);// api.value(0)  dataItem 
								var start = api.coord([api.value(1), categoryIndex]); //  api.coord(...) 
								var end = api.coord([api.value(2), categoryIndex]);
								var height = api.size([0, 1])[1] * 5;
								var containerHeight = api.getHeight(); // 
								var customHeight = 30; // 
								var yOffset = (containerHeight - customHeight) / 4
								// console.log(api.style), '2222');
								return {
									type: 'rect',//  'circle', 'sector', 'polygon' 

									align: 'center',
									shape: echarts.graphic.clipRectByRect({ // 
										x: start[0],
										y: start[1] - height / 2,
										width: end[0] - start[0] +1 ,
										height: height
									}, { // 
										x: params.coordSys.x,
										y: params.coordSys.y + yOffset,
										width: params.coordSys.width,
										height: customHeight,
									}),
									style: api.style(),

								};
							},
							encode: {
								x: [0,1], // data 12 X 
								y: 0,	// data 0 Y 

							},
							data: datas
						}
					]
				};
				if (option && typeof option === "object") {
					myChart.setOption(option, true);
				}
			}
		}
		onMounted(() => {
			methods.drawing()
		})
		watch(()=>state.data,(newVal,oldVal)=>{
			if(newVal) {
				methods.drawing()
			}
		})
		return { ...toRefs(state), ...methods, line }
	}
}
</script>

<style lang="scss" scoped>
.start_stop{
	width: 100%;
	height: 100%;
}
</style>